.wrapNavAb{
    /* background-color: whitesmoke; */
    justify-content: center;
    align-items: center;
    display: flex;
}
.navAbout{
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: red; */
    width: 95%;
}
.navAbout img{
    margin-right: 180px;
}
.navAbout ul{
    display: flex;
    flex-direction: row;
}
.navAbout ul li{
    list-style: none;
    margin-top: 7px;
    margin-right: 35px;
}
.navAbout ul li a{
    text-decoration: none;
    font-size: 17px;
    color: white;
}
.lst {
    padding-left: 80px;
    color: white;
}
.navAbout .buttonNavAb1{
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid white 1px;
    color: white;
    border-radius: 15px;
    width: 100px;
    height: 25px;
    margin-top: 2px;
    cursor: pointer;
}
.navAbout ul li a:hover{
    color: whitesmoke;
    font-weight: bolder;
}