
body{
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
 }

 .navbar{
    margin-top: 40px;
    margin-left: 40px;
 }
 .wrapNav img{
    margin-right: 150px;
 }
 .wrapNav ul{
    display: flex;
    flex-direction: row;
 }
 .wrapNav ul li{
  list-style: none;
  margin-top: 7px;
  margin-right: 35px;
 }
.wrapNav ul li a{
    text-decoration: none;
    font-size: 17px;
    color: gray;
}
.navbar .wrapNav ul li a:hover{ 
    color: black;
    transition: ease-in-out 0,8;
    cursor: pointer;
    font-weight: bolder;
}

.wrapNav .last{
    margin-left: 120px;
    font-weight: bolder;
    color: black;
    cursor: pointer;
}
.wrapNav .button2{
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid #1e90ff 1px;
    color: #1e90ff;
    border-radius: 15px;
    width: 120px;
    height: 30px;
    margin-top: -1px;
    cursor: pointer;
}


.Main404{
    margin-top: 170px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.wrap404-1{
    height: 300px;
    margin-right: 150px;
    margin-bottom: 10px;
}
.wrap404-1 .button404{
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid #246bc9 1px;
    background-color: #246bc9;
    color: white;
    border-radius: 15px;
    width: 120px;
    height: 30px;
    margin-top: -1px;
    cursor: pointer;
}
.wrap404-2{
    margin-left: 100px;
}