body{
    margin: 0;
    padding: 0;

}
.sign{
    display: flex;
}
.cont1{
    font-family: Arial, Helvetica, sans-serif;
    width: 34%;
    height: 773px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.wrap1{
    background-color: whitesmoke;
    height: 100%;
    width: 100%;
}
.content{
    margin-left: 20px;
}
.cont1 .logo{
    margin-top: 20px;
    margin-bottom: 85px;
}
.cont1 h5{
    font-size: 25px;
    text-align: center;
}
.cont1 p{
    text-align: center;
    color: grey;
}
.cont1 h6{
    margin-top: 50px;
    color: gray;
}


.cont2{
    width: 100%;
    height: 773px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
}
.wrap2{
    width: 40%;
}
.cont2 h2{
    font-size: 30px;
}
.cont2 h4{
    font-size: 12px;
}
.cont2 h5 span{
 color: #1e90ff;
}
.login{
    height: 40px;
    width: 386px;
    border: solid gray 1px;
    border-radius: 5px;
}
.butonLogin{
    background-color: #1e90ff;
    height: 40px;
    width: 387px;
    border: #1e90ff 1px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
.button-log{
    text-decoration: none;
}
.content2 h4 span{
    color: gray;
    margin-right: 1%;
}



 
