.navbar{
    margin-top: 40px;
    margin-left: 40px;
 }
 .wrapNav img{
    margin-right: 180px;
 }
 .wrapNav ul{
    display: flex;
    flex-direction: row;
 }
 .wrapNav ul li{
  list-style: none;
  margin-top: 7px;
  margin-right: 35px;

 }
.wrapNav ul li a{
    text-decoration: none;
    font-size: 17px;
    color: gray;
}
.navbar .wrapNav ul li a:hover{ 
    color: black;
    transition: ease-in-out 0,8;
    cursor: pointer;
    font-weight: bolder;
}

.wrapNav .trakhir{
    margin-left: 120px;
    margin-right: 50px;
    font-weight: bolder;
    color: black;
    cursor: pointer;
    text-decoration: none;
}
.wrapNav .button2{
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid #1e90ff 1px;
    color: #1e90ff;
    border-radius: 15px;
    width: 120px;
    height: 30px;
    margin-top: -1px;
    cursor: pointer;
}
